<template>
  <div>
    <table>
      <thead>
        <tr>
          <th>Nom</th>
          <th>Commentaires</th>
          <th>Degré d'alcool</th>
          <th>Type</th>
          <th>Prix HT</th>
          <th>Prix TTC</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(beer, index) in beers"
          :key="index"
        >
          <td>
            {{ beer.name | upperCaseFilter }}
          </td>
          <td>
            {{ beer.comment }}
          </td>
          <td>
            {{ beer.alcoholDegree }}
          </td>
          <td>
            {{ beer.type }}
          </td>
          <td>
            {{ beer.priceHT }}
          </td>
          <td>
            <price-ttc
              v-model="beer.priceHT"
              :canEdit="false"
            />
          </td>
          <td>
            <button
              type="button"
              @click="updateBeer(beer)"
            >
              Modifier
            </button>
            <button
              type="button"
              @click="deleteBeer(beer)"
            >
              Supprimer
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import beersApi from "../api/beersApi";
import priceTtc from "../components/priceTtc";
export default {
  name: 'BeerList',
  components: {
    priceTtc
  },
  data() {
    return {
      beers: []
    };
  },
  computed: {
    ...mapGetters({
      owner: "getOwner"
    })
  },
  methods: {
    updateBeer(beer) {
      this.$router.push({ name: 'BeerForm', params: { id: beer.id } });
    },
    async getAllBeer() {
      try {
        const response = await beersApi.getAllBeer(this.owner);
        this.beers = response.body.content;
      } catch (error) {
        /* eslint-disable no-console */
        console.error('Une erreur est survenue lors de la récupération des bières');
        /* eslint-enable no-console */
      }
    },
    async deleteBeer(beer) {
      try {
        await beersApi.removeBeer(beer.id, this.owner);
        await this.getAllBeer();
      } catch (error) {
        /* eslint-disable no-console */
        console.error('Impossible de supprimer la bière : ', beer);
        /* eslint-enable no-console */
      }
    }
  },
  async mounted() {
    await this.getAllBeer();
  }
};
</script>

