import Vue from 'vue';
const BASE_URL = 'http://163.172.53.19:8081/api/beers';

export default {
  getBeer(beerId, owner) {
    return Vue.http.get(`${BASE_URL}/${beerId}`, {
      params: {
        owner
      }
    });
  },
  getAllBeer(owner) {
    return Vue.http.get(`${BASE_URL}`, {
      params: {
        owner
      }
    });
  },
  createBeer(beer, owner) {
    return Vue.http.post(`${BASE_URL}?owner=${owner}`, {
      ...beer
    });
  },
  updateBeer(beer, owner) {
    return Vue.http.put(`${BASE_URL}/${beer.id}?owner=${owner}`, {
      ...beer,
      owner
    });
  },
  removeBeer(beerId, owner) {
    return Vue.http.delete(`${BASE_URL}/${beerId}?owner=${owner}`);
  }
};
