<template>
  <div>
    <form @submit.prevent="saveBeer">
      <p>
        Nom :
        <input
          v-model="beer.name"
          type="text"
          required
        >
      </p><div v-if="errorMessage.name">
        {{ errorMessage.name }}
      </div>
      <p>
        Commentaires :
        <textarea v-model="beer.comment" />
      </p>
      <p>
        Prix HT :
        <input
          v-model="beer.priceHT"
          type="number"
          required
        >
      </p><div v-if="errorMessage.priceHT">
        {{ errorMessage.priceHT }}
      </div>
      <p>
        Prix TTC:
        <price-ttc
          v-model="beer.priceHT"
          :canEdit="true"
          @updatePriceHt="updatePriceHt"
        />
      </p>
      <p>
        Degré d'alcool :
        <input
          v-model="beer.alcoholDegree"
          type="number"
          min="0"
          max="70"
          step="any"
          required
        >
      </p><div v-if="errorMessage.alcoholDegree">
        {{ errorMessage.type }}
      </div>
      <p>
        <select
          v-model="beer.type"
          required
        >
          <option
            v-for="type in beerType"
            :key="type"
            :value="type"
          >
            {{ type }}
          </option>
        </select>
      </p><div v-if="errorMessage.type">
        {{ errorMessage.type }}
      </div>
      <button
        type="button"
        @click="goToList"
      >
        Annuler
      </button>
      <button
        type="submit"
        :disabled="!formIsValid"
      >
        Sauvegarder
      </button>
      <p v-if="genericError">
        Une erreur est survenue lors de la création ou de la modification d'une bière.
      </p>
    </form>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import PriceTtc from './priceTtc';
import beersApi from '../api/beersApi';

export default {
  name: 'BeerForm',
  components: { PriceTtc },
  data() {
    return {
      genericError: false,
      beer: {
        name: '',
        comment: '',
        priceHT: 0,
        alcoholDegree: 0,
        type: '',
        id: 0,
        owner: ''
      },
      beerType: [
        "BLONDE",
        "IPA",
        "BROWN",
        "DARK"
      ],
      errorMessage: {},
      isUpdate: false
    };
  },
  computed: {
    formIsValid() {
      return this.beer.name !== '' && this.beer.priceHT !== '' && this.beer.alcoholDegree !== '' && this.beer.type !== '';
    },
    ...mapGetters({
      currentBeer: "getCurrentBeer",
      owner: 'getOwner'
    })
  },
  methods: {
    goToList() {
      this.$store.commit('resetCurrentBeer');
      this.$router.push('/');
    },
    async saveBeer() {
      try {
        if (this.formIsValid) {
          if (this.isUpdate) {
            await beersApi.updateBeer(this.beer, this.owner);
          } else {
            await beersApi.createBeer(this.beer, this.owner);
          }
          this.goToList();
        } else {
          if (this.beer.name === '') {
            this.errorMessage.name = 'Le nom est obligatoire.';
          }
          if (this.beer.priceHT === '') {
            this.errorMessage.priceHT = 'Le prix est obligatoire.';
          }
          if (this.beer.type === '' || !this.beerType.includes(this.beer.type)) {
            this.errorMessage.type = 'Le type est obligatoire et doit être d\'une des valeurs suivantes BLONDE, BROWN, IPA, DARK';
          }
          if (this.beer.alcoholDegree === '' || this.beer.alcoholDegree < 0 || this.beer.alcoholDegree > 70) {
            this.errorMessage.type = 'Le degré d\'alcool est obligatoire et doit être compris entre 0 et 70';
          }
        }
      } catch (error) {
        this.genericError = true;
        console.error('Erreur lors de la création ou la modification d\'une bière');
      }
    },
    updatePriceHt(price) {
      this.beer.priceHT = price;
    },
    async getBeer(beerId) {
      try {
        const response = await beersApi.getBeer(beerId, this.owner);
        this.beer = response.body;
      } catch (error) {
        console.error('Impossible de récupérer la bière avec id =', beerId);
      }
    }
  },
  mounted() {
    if (this.$route.params.id && this.$route.params.id !== '' && this.$route.params.id !== 0) {
      this.isUpdate = true;
      this.getBeer(this.$route.params.id);
    }
  }
};
</script>

