<template>
  <div>
    <input
      v-if="canEdit"
      v-model="priceHT"
      type="number"
      min="0"
      step="any"
      @input="sendPrice"
    >
    <p v-else>
      {{ priceHT }}
    </p>
  </div>
</template>

<script>
export default {
  name: 'priceHtTtc',
  props: {
    value: {
      required: true
    },
    canEdit: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      priceHT: null
    };
  },
  watch: {
    value(newVal, oldVal) {
      if (newVal !== oldVal) {
        if (this.value !== '') {
          const priceHT = Number(this.value * 1.20).toFixed(2);
          this.priceHT = priceHT;
        }
      }
    }
  },
  methods: {
    sendPrice() {
      this.$emit('updatePriceHt', Number(this.priceHT / 1.20).toFixed(2));
    }
  }
};
</script>
